<script>
import HeadTop from '@/components/HeadTop' 
import { reactive, toRefs, computed, watch } from "vue";
import { useI18n } from "vue-i18n";
import {useStore} from "vuex";
import config from "@/utils/config";
import tokenAbi from "@/utils/abi";
import investAbi from "@/utils/investAbi";
import Clipboard from 'clipboard';
import { Toast } from 'vant';
import BigNumber from 'bignumber.js';
import { showNotice } from "@/storage"
export default {
  components: {
    HeadTop
  },
  setup() {
    var clipboard = new Clipboard('.copy')
    clipboard.on('success', function (e) {
      Toast(t('share.复制成功'))
      e.clearSelection()
    })
    clipboard.on('error', function () {
      Toast(t('share.复制失败，请手动长按复制'))
    }) 
    const store = useStore()
    const { t } = useI18n()
    const data = reactive({
      $t: t,
      address: computed(() => store.state.accounts[0]),
      web3: computed(() => store.state.provider),
      investContract: null,
      abdxContract: null,
      isDraw: false,
      isShowDialog: !showNotice.get(),
      userInfo: {
        investAmmount: '0.00',
        haveBack: '0.00',
        cycle: 0
      },
      rate: '100',
      staticAmount: '0.00',
      initContract: () => {
        data.investContract = new data.web3.eth.Contract(investAbi, config.investAddress)
        data.abdxContract = new data.web3.eth.Contract(tokenAbi, config.abdxAddress)
        data.initData()
      },
      initData: () => {
        data.getUserInfo()
        data.getStaticReward()
        data.getRate()
      },
      getUserInfo: async () => {
        let userInfo = await data.investContract.methods.getUserInfo(data.address).call()
        let decimals = await data.abdxContract.methods.decimals().call()
        data.userInfo.investAmmount = new BigNumber(userInfo.investAmmount).dividedBy(10 ** decimals).toFixed(2)
        data.userInfo.haveBack = new BigNumber(userInfo.haveBack).dividedBy(10 ** decimals).toFixed(2)
        let userLimit = await data.investContract.methods.userLimit(data.address).call();
        let limit =  new BigNumber(userLimit).dividedBy(10 ** decimals).toFixed()
        data.userInfo.cycle = Math.log2(limit > 100000 ? parseInt(limit / 100000) : 1) + 1
        console.log(userInfo)
      },
      confirm: () => {
        showNotice.set('off')
      },
      getRate: async () => {
        let price = await data.investContract.methods.tokenPrice().call();
        data.rate = new BigNumber(price).dividedBy(10000).toFixed()
      },
      getStaticReward: async () => {
        let amount = await data.investContract.methods.getRewardAmount(data.address).call()
        let decimals = await data.abdxContract.methods.decimals().call()
        data.staticAmount = new BigNumber(amount).dividedBy(10 ** decimals).toFixed(2)
      },
      receiveStaticReward: async () => {
        // let gas = await data.investContract.methods.withdrawReward().estimateGas({from: data.address})
        data.isDraw = true;
        try {  
          await data.investContract.methods.withdrawReward().send({from: data.address})
          data.isDraw = false;
          Toast('领取成功')
        } catch (error) {
          Toast('领取失败')
          data.isDraw = false;
        }
        
        data.initData()
      }
    })
    if (data.address) {
      data.initContract()
    }
     watch(() => data.address, (val) => {
      if (val) {
        data.initContract()
      }
    })
    const refData = toRefs(data);
    return {
      ...refData
    };
  }
}
</script>

